:root {
  --section-bcg-color: #fffdee;
  --section-dark-bcg-color: #f0ecd6;
  --footer-bcg-color: #0e0c07;
  --main-title-color: #fff;
  --accent-color: #f3c653;
  --accent-dark-color: #c79002;
  --btn-menu-title-color: #000;
  --transparent-btn-color: rgba(0, 0, 0, .4);
  --logo-color: #494848;
  --main-text-color: #7a7a7a;
  --light-text-color: #b4b4b4;
  --address-hero-color: #e5e5e5;
  --contacts-color: #828282;
  --defolt-animation: 250ms cubic-bezier(.4, 0, .2, 1);
}

* {
  border: 0;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
}

input:focus {
  outline: rgba(0, 0, 0, 0);
}

p {
  margin: 0;
}

a {
  color: inherit;
  margin: 0;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  color: inherit;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  text-decoration: none;
  list-style: none;
}

a:active, a:visited {
  color: inherit;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

textarea {
  resize: none;
}

textarea:focus {
  outline: rgba(0, 0, 0, 0);
}

button, input[type="submit"], input[type="reset"] {
  color: inherit;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  background: none;
  border: none;
  padding: 0;
}

button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (min-width: 414px) {
  .container {
    width: 414px;
  }
}

@media (min-width: 1024px) {
  .container {
    width: 960px;
  }
}

@media (min-width: 1280px) {
  .container {
    width: 1140px;
  }
}

body {
  font-family: Montserrat;
}

.footer {
  background: var(--footer-bcg-color);
  padding: 40px 0;
}

@media (min-width: 1024px) {
  .footer {
    padding: 80px 0;
  }

  .footer-leftside {
    display: flex;
  }

  .footer-social-list {
    flex-direction: column;
    display: flex;
  }

  .footer-social-list-item {
    border: .5px solid var(--accent-color);
    width: 144px;
    transition: background-color var(--defolt-animation);
    border-radius: 2px;
    margin-right: 0;
  }

  .footer-link {
    padding: 10px 12px;
  }

  .footer-social-list-item:not(:last-child) {
    margin-bottom: 10px;
  }

  .footer-nav {
    margin-right: 137px;
  }
}

.footer > .container {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.footer-logo {
  letter-spacing: .3em;
  color: var(--accent-color);
  font-family: Film P3;
  font-size: 40px;
  font-weight: 700;
  line-height: 1.07;
}

@media (min-width: 1024px) {
  .footer-logo {
    font-size: 64px;
  }
}

.footer-nav {
  letter-spacing: .02em;
  color: var(--accent-color);
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

@media (min-width: 1280px) {
  .footer-nav {
    margin-right: 200px;
  }
}

.footer-social-list {
  letter-spacing: .02em;
  color: var(--accent-color);
  font-family: Montserrat;
  font-size: 16px;
  line-height: 1.25;
  display: flex;
}

@media (max-width: 1023.98px) {
  .footer-social-list-item > a > span {
    display: none;
  }

  .footer-social-list-item:not(:last-child) {
    margin-right: 23px;
  }
}

.footer-icon {
  fill: var(--accent-color);
}

.footer-nav-list-item:not(:last-child) {
  margin-bottom: 16px;
}

.footer-nav-list-item:last-child {
  margin-bottom: 33px;
}

.footer-link {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.footer-social-list-item:hover, .footer-social-list-item:focus, .footer-social-list-item:active {
  background-color: var(--main-title-color);
}

